<template>
    <div class="online-avatar-group flex-row  justify-between items-center">
        <div v-for="(avatar, index) in avatars" :key="index" class="online-avatar-item">
            <img :src="avatar.src" alt="Avatar" class="avatar-image">
        </div>
    </div>

    <div class="social-identity-tabs sticky-top">
        <div
        class="identity-tab"
        v-for="(title, index) in tabTitles"
        :key="index"
        :class="{ 'active-tab': index === activeTab }"
        @click="activeTab = index"
        >
        {{ title }}
        </div>
  </div>

    <div class="flex-col justify-start items-center">
      <Card
      :avatarSrc="data1.avatarSrc"
      :headText1="data1.headText1"
      :textgroup2First="data1.textgroup2First"
      :textgroup2Second="data1.textgroup2Second"
      :showTextGroup="data1.showTextGroup"
      :card2Text="data1.card2Text"
      :showImageGroup="data1.showImageGroup"
      :imageSources="data1.imageSources"
      ></Card>
      <Card
      :avatarSrc="data2.avatarSrc"
      :headText1="data2.headText1"
      :textgroup2First="data2.textgroup2First"
      :textgroup2Second="data2.textgroup2Second"
      :showTextGroup="data2.showTextGroup"
      :card2Text="data2.card2Text"
      :showImageGroup="data2.showImageGroup"
      :imageSources="data2.imageSources"
      ></Card>
    </div>

    <div class="add-social" @click="onClickAdd" :style="{backgroundColor:keyColor}"></div>




</template>
      
  <script>
  import Card from '@/components/SocialCard.vue';
  import { ref } from 'vue';
  import { computed } from 'vue';
  import { useStore } from 'vuex';
  export default {
      setup() {
          const activeBar = ref(0);
          const store = useStore();
          const switchStatus = computed(() => store.getters['switchStatus']);
          const keyColor = computed(() => switchStatus.value ? '#1FAFFF' : '#00C121');
          return {keyColor,activeBar };
      },
      components: {
        Card,
      },
      props: {
  
      },
      data() {
          return {

            tabTitles: ["", "", "  "],
            avatars: [
                { src: require('@/assets/person/4.png') },
                { src: require('@/assets/person/5.png') },
                { src: require('@/assets/person/6.png') },
                { src: require('@/assets/person/2.png') },
            ],

            data1: {
                avatarSrc: require('@/assets/person/3.png'),
                headText1: '2023',
                textgroup2First: '',
                textgroup2Second: '',
                showTextGroup: true,
                card2Text: '',
                showImageGroup: true,
                imageSources: [
                    require('@/assets/share_publish/1.png'),
                    require('@/assets/share_publish/2.png'),
                    require('@/assets/share_publish/3.png'),
                ]
            },
            data2: {
                avatarSrc: require('@/assets/person/7.png'),
                headText1: '',
                textgroup2First: ' ',
                textgroup2Second: ' ',
                showTextGroup: false,
                card2Text: '',
                showImageGroup: true,
                imageSources: [require('@/assets/share_publish/mama.png')]
            },
  
          };
      },
      computed: {
      },
      methods: {

          onClickAdd(){
              this.$router.push({name:'share'})
          }
      },
    created() {
    //  activeTab  0
        this.activeTab = 0;
    },
  
  
  };
  </script>
<style>
.online-avatar-group {
    width: 85%;
    padding: 0.5rem;
    margin:1rem 2.3rem;
}

.online-avatar-item {
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    width: 3.5rem;
    height: 3.5rem;
    border: solid 0.19rem rgb(0, 193, 32);
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2); 
    
    display: flex; /*  flex  */
    justify-content: center; /*  */
    align-items: center; /*  */
}

.avatar-image {
    display: block; 
    width: 90%;
    height: 90%; 
    border-radius: 50%;
    object-fit: cover;

}

.social-identity-tabs {
    position: relative;

    margin: 0.5rem 1.5rem;
    padding:0.5rem 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
}

.identity-tab {
  padding: 5px 20px;
  border-radius: 30px; /*  */
  background-color: rgba(255, 208, 0, 0.3); /*  */
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  color:rgba(51,21,0,1);
  transition: background-color 0.3s ease;
}

.identity-tab:hover {
  background-color: rgba(255, 208, 0, 1); /*  */
}

.active-tab {
  background-color: rgba(255, 208, 0, 1); /*  */
}

.sticky-top {
  /*  */
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0; /*  0px  */
  z-index: 5;
}

.add-social{

    background-color: rgb(0, 193, 32);
    border-radius: 50%;
    height: 4.38rem;

    background-image: url("@/assets/add.png");
    box-shadow: 0 5px 6px rgba(0, 0, 0, 0.3);
    background-size:30%; /*  */
    background-position:calc(100% - 21.5px) center;
    background-repeat: no-repeat;

    position: fixed;
    right:10px;
    bottom: 5rem; /*  20px */
    width: 60px; /*  */
    height: 60px; /*  */
    cursor: pointer; /*  */
    z-index: 10; /*  */
}
@media (min-width: 1025px) {
    .add-social{
            right:auto;
            transform: translateX(320px);
        }
}
</style>

